













import Vue from 'vue';

export default Vue.extend({
  data: () => ({
    select: { state: '', abbr: 'rel' },
    items: [
      { state: '', abbr: 'created' },
      { state: '', abbr: 'rel' },
      { state: '', abbr: 'stock' },
    ],
  }),
  mounted() {
    this.$emit('change', this.select.abbr);
  },
  watch: {
    select() {
      this.$emit('change', this.select.abbr);
    },
  },
});
