










import Vue from 'vue';

export default Vue.extend({
  props: {
    label: String,
    switch: {
      type: Boolean,
      default: false,
    },
    disabled: {
      type: Boolean,
      default: false,
    },
  },
  // tslint:disable-next-line
  data () {
    return {
      tmpSwitch: this.switch,
    };
  },
  watch: {
    tmpSwitch(switchFlag: boolean) {
      this.$emit('change', switchFlag);
    },
  },
  mounted() {
    this.$emit('change', this.tmpSwitch);
  },
});
